import React, { useState } from "react";

export default function Link(props) {
    const [col, setCol] = useState("gray-300");

    return (
        <a href={props.url} target="_blank" rel="noopener noreferrer" className="sm:w-12 sm:h-12 w-8 h-8 rounded-lg hover:bg-custom-500 focus:bg-custom-500 flex items-center justify-center sm:mx-1 mx-2" onMouseEnter={() => {props.enter(); setCol("custom-200");}} onMouseLeave={() => {setCol("gray-300");}}>
            <i className={`ri-${props.name}-line text-2xl text-${col}`} />
        </a>
    );
}
