import React, { useState } from "react";

export default function Project(props) {
    const [index, setIndex] = useState(0);
    const [color_l, setColorL] = useState("custom-100");
    const [color_r, setColorR] = useState("custom-100");

    let temp;

    const tags = (name) => ({
        main: <div className="bg-green-400  uppercase sm:w-16 sm:h-8 w-16 h-6 rounded-lg sm:text-sm text-xs flex items-center justify-center font-bold font-mono tracking-wide text-gray-900 mx-2">{name}</div>,
        sec:  <div className="bg-yellow-400 uppercase sm:w-32 sm:h-8 w-20 h-6 rounded-lg sm:text-sm text-xs flex items-center justify-center font-bold font-mono tracking-wide text-gray-900 mx-2">{name}</div>
    });

    const indexSetter = (id) => {
        temp = id + index;
        if (temp < 0) {
            setIndex(props.images.length - 1);
        } else if (temp >= props.images.length) {
            setIndex(0);
        } else {
            setIndex(temp);
        }
    };

    return(
        <div className="flex sm:flex-row flex-col w-screen sm:h-68 sm:pt-2 sm:justify-around sm:items-top justify-center items-center sm:mt-4 mt-2 bg-custom-500 sm:pb-4">
            <div className="sm:w-64 w-20 sm:h-64 h-20 p-2 sm:mb-0 mb-2 flex items-center justify-between m-auto">
                <img src={props.logo} alt="" />
            </div>
            <div className="sm:w-1/3 w-5/6 sm:h-64 flex flex-col items-center justify-between sm:mb-0 mb-4">
                <div className="w-full text-center sm:h-16 flex items-center justify-center font-montserrat font-bold sm:text-2xl text-lg text-custom-200 tracking-wider uppercase">{props.name}</div>
                <div className="w-full text-center sm:h-32 py-2 px-4 font-mulish tracking-wide text-gray-500 sm:text-md text-xs rounded border-2 border-gray-500 sm:my-0 my-4 flex items-center justify-center">
                    {props.desc}
                </div>
                <div className="w-full text-center sm:h-12 flex items-center justify-center mt-2">
                    {tags(props.main).main}
                    {props.secs ? props.secs.map(sec => tags(sec).sec) : null}
                </div>
            </div>
            <div className="sm:w-2/5 w-11/12 sm:h-64 flex items-center justify-between sm:mb-0 mb-4">
                <button className="sm:w-16 sm:h-16 w-8 h-8 mx-auto bg-gray-700 hover:bg-gray-800 focus:bg-gray-800 font-bold font-sans tracking-wide rounded-full" onMouseOver={() => setColorL("custom-200")} onMouseOut={() => setColorL("custom-100")} style={{cursor:'pointer'}} onClick={() => indexSetter(-1)}>
                    <i className={`ri-arrow-left-fill  text-${color_l} sm:text-2xl text-xl`}></i>
                </button>
                <div className="sm:w-96 sm:h-54 w-64 h-36 bg-teal-400 my-auto flex items-center justify-center bg-custom-500">
                    <img src={props.images[index]} alt="" />
                </div>
                <button className="sm:w-16 sm:h-16 w-8 h-8 mx-auto bg-gray-700 hover:bg-gray-800 focus:bg-gray-800 font-bold font-sans tracking-wide rounded-full" onMouseOver={() => setColorR("custom-200")} onMouseOut={() => setColorR("custom-100")} style={{cursor:'pointer'}} onClick={() => indexSetter(1)}>
                    <i className={`ri-arrow-right-fill text-${color_r} sm:text-2xl text-xl`}></i>
                </button>
            </div>
        </div>
    );
}
