import React, { useState } from "react";
import Link from "./Link";

export default function Navbar() {
    const old = "EdgeKing810's Portfolio";

    const [len, setLen] = useState(0);
    const [opacity, setOpacity] = useState(1);

    const [timer, setTimer] = useState(100);
    const [title, setTitle] = useState(old);

    setTimeout(() => {
        if (len < title.length) {
            setLen(prev => prev += 1);
        } else {
            opacity === 1 ? setOpacity(0) : setOpacity(1);
            setTimer(600);
            setLen(100);
        }
    }, timer);

    return(
        <div className="sm:h-screen w-screen flex flex-col items-center justify-center sm:my-0 my-4">
            <div className="w-full px-2 flex justify-center items-center font-karla text-gray-500 sm:text-4xl text-xl font-bold tracking-widest">
                <div className="">
                    {title.split("").slice(0, len)}
                </div>
                <div className="sm:h-8 w-2 h-4 sm:ml-2 ml-1 bg-custom-200 sm:mb-2 sm:mt-1 mb-1" style={{'opacity': opacity}}></div>
            </div>
            <div className="w-full px-4 text-center sm:py-0 py-2 flex justify-center items-center font-montserrat text-gray-600 sm:text-md text-xambitiouss tracking-wider">
                A list of my skills and a showcase of the most ambitious projects I worked on
            </div>

            <div className="flex sm:w-full text-center items-center justify-center mt-2"
                 onMouseLeave={() => setTitle(old)} 
                 style={{pointerEvents: (timer === 600) ? 'inherit' : 'none'}}>

                    <Link url="mailto:kishan@kinesis.games" name="mail" enter={() => setTitle("Send me a mail")} />
                    <Link url="https://facebook.com/com.KinesisGames.KishanTakoordyal" name="facebook" enter={() => setTitle("Check my profile on Facebook")} />
                    <Link url="https://www.instagram.com/kishan_takoordyal/" name="instagram" enter={() => setTitle("Visit my Instagram page")} />
                    <Link url="https://twitter.com/EdgeKing810" name="twitter" enter={() => setTitle("Tweet me")} />
                    <Link url="https://linkedin.com/in/kishan-takoordyal-99816b180" name="linkedin" enter={() => setTitle("Check out my profile on LinkedIn")} />
                    <Link url="https://links.kinesis.games" name="add" enter={() => setTitle("Visit my other sites")} />

            </div>
        </div>
    );
}
